<template>
	<view class="container">
		<!-- È°µÈù¢Â§¥ÈÉ® -->
		<view class="page-header">
		<view class="page-header-title">
				<text class="page-title">ÂÖ•Â∫ìÁÆ°ÁêÜ</text>
			</view>
			<view class="page-actions">
				<view class="header-btn primary" @tap="goAdd">Êñ∞Âª∫ÂÖ•Â∫ìÂçï</view>
			<view class="header-btn ghost" @tap="resetFilters">ÈáçÁΩÆ</view>
			<view class="header-btn ghost" @tap="generateList">Êü•ËØ¢</view>
		</view>
	</view>
	
	<!-- Áä∂ÊÄÅÁ≠õÈÄâTabÊ†èÔºàÊñ∞ËÆæËÆ°Ôºâ-->
	<view class="status-tabs">
		<view 
			v-for="status in statusList" 
			:key="status.value"
			class="status-tab"
			:class="{ active: statusFilter === status.value }"
			@tap="changeStatusFilter(status.value)"
		>
			<text class="status-name">{{ status.label }}</text>
			<text class="count" v-if="statusCounts[status.value] !== undefined">
				{{ statusCounts[status.value] }}
			</text>
			</view>
		</view>
		
		<filter-panel
			class="panel-wrapper"
			:keyword="searchKeyword"
		keyword-placeholder="ÊêúÁ¥¢ÂçïÂè∑/ËçØÊùêÂêçÁß∞"
			:show-date="true"
			:start-date="startDate"
			:end-date="endDate"
			:quick-filters="quickFilters"
			:active-quick-filter="selectedQuickFilter"
			:show-search-button="false"
			@update:keyword="onKeywordUpdate"
			@update:startDate="onStartDateUpdate"
			@update:endDate="onEndDateUpdate"
			@quick-filter="selectQuickFilter"
			@date-change="onDateRangeChange"
			@search="generateList"
		>
		</filter-panel>
		
		<view class="result-meta">
		<text class="meta-item">Â∑≤ÈÄâÊã© {{ recordList.length }} Á¨î</text>
			<text class="meta-dot">‚Ä¢</text>
		<text class="meta-item">ÂÖ±ËÆ° {{ totalDrugs }} ÁßçËçØÊùê</text>
		</view>
		
		<!-- ÂàóË°® -->
		<view class="list-container">
			<view 
				v-for="item in recordList" 
				:key="item._id"
				class="record-card"
				@click="goDetail(item._id, item)"
			>
				<!-- ÂçïÂè∑ÂíåÁä∂ÊÄÅ -->
				<view class="record-header">
					<view class="record-no">{{ item.recordNo }}</view>
					<view :class="['record-status', `status-${item.status}`]">
						{{ getStatusText(item.status) }}
					</view>
				</view>
				
				<!-- Âü∫Êú¨‰ø°ÊÅØ -->
				<view class="record-info">
					<view class="info-item">
						<text class="info-label">Êìç‰Ωú‰∫∫Ôºö</text>
						<text class="info-value">{{ item.operator }}</text>
					</view>
					<view class="info-item">
						<text class="info-label">ÂàõÂª∫Êó∂Èó¥Ôºö</text>
						<text class="info-value">{{ item.createTime }}</text>
					</view>
					<view class="info-item">
					<text class="info-label">ËçØÊùêÁßçÁ±ªÔºö</text>
						<text class="info-value">{{ item.items.length }} Áßç</text>
					</view>
				</view>
				
				<!-- ÂæÖÂ§çÊ†∏Êó∂ÊòæÁ§∫Êìç‰ΩúÊåâÈíÆ -->
				<view v-if="item.status === 'pending_review' && canReview(item)" class="record-actions">
					<u-button 
						text="ÂéªÂ§çÊ†∏" 
						type="primary" 
						size="small"
						@click.stop="goReview(item._id)"
					></u-button>
				</view>
				
				<!-- Â∑≤È©≥ÂõûÊó∂ÊòæÁ§∫È©≥ÂõûÂéüÂõ† -->
				<view v-if="item.status === 'rejected'" class="record-actions">
					<view class="reject-reason">È©≥ÂõûÂéüÂõ†Ôºö{{ item.rejectReason }}</view>
				</view>
			</view>
			
			<!-- Á©∫Áä∂ÊÄÅ -->
			<view v-if="recordList.length === 0" class="empty-state">
				<text class="empty-icon">üìã</text>
				<text class="empty-text">
					ÊöÇÊó†{{ statusLabel === 'ÂÖ®ÈÉ®' ? '' : statusLabel }}ÂÖ•Â∫ìÂçï
				</text>
			</view>
			
			<!-- Âä†ËΩΩÊõ¥Â§ö -->
			<view v-if="hasMore" class="load-more" @click="loadMore">
				<text>Âä†ËΩΩÊõ¥Â§ö</text>
			</view>
			<view v-else-if="recordList.length > 0" class="no-more">
				<text>Ê≤°ÊúâÊõ¥Â§ö‰∫Ü</text>
			</view>
		</view>
	</view>
</template>

<script>
import FilterPanel from '@/components/filter-panel/index.vue'

export default {
	components: {
		FilterPanel
	},
	data() {
		return {
			currentTime: '',
			recordList: [],
			page: 1,
			pageSize: 10,
			hasMore: true,
			currentUserId: '',
			statusFilter: 'all',
			
			// Áä∂ÊÄÅÂàóË°®ÈÖçÁΩÆÔºàÊñ∞ËÆæËÆ°Ôºâ
			statusList: [
				{ label: 'ÂÖ®ÈÉ®', value: 'all' },
				{ label: 'ËçâÁ®ø', value: 'draft' },
				{ label: 'ÂæÖÂ§çÊ†∏', value: 'pending_review' },
				{ label: 'Â∑≤È©≥Âõû', value: 'rejected' }
			],
			
			// ÂêÑÁä∂ÊÄÅÊï∞ÈáèÁªüËÆ°
			statusCounts: {
				all: 0,
				draft: 0,
				pending_review: 0,
				completed: 0,
				rejected: 0
			},
			statsData: {
				today: 0,
				thisWeek: 0,
				thisMonth: 0,
				pending: 0
			},
			statusSummary: {
				all: 0,
				draft: 0,
				pending_review: 0,
				completed: 0,
				rejected: 0
			},
			// ÊêúÁ¥¢Áõ∏ÂÖ≥
			searchKeyword: '',
			searchTimer: null,
			// Êó•ÊúüÁ≠õÈÄâÁõ∏ÂÖ≥
			startDate: '',
			endDate: '',
			selectedQuickFilter: 'month',
			quickFilters: [
				{ label: 'ÂÖ®ÈÉ®', value: 'all' },
				{ label: '‰ªäÂ§©', value: 'today' },
				{ label: 'Êú¨Âë®', value: 'week' },
				{ label: 'Êú¨Êúà', value: 'month' },
				{ label: 'Ëá™ÂÆö‰πâ', value: 'custom' }
			]
		}
	},
	
	onLoad(options) {
		// Â¶ÇÊûú‰ªéËçØÊùêÁÆ°ÁêÜÈ°µÈù¢Â∏¶‰∫ÜÁä∂ÊÄÅÂèÇÊï∞Ôºà‰æãÂ¶Ç status=pending_reviewÔºâÔºå‰ºòÂÖà‰ΩøÁî®ËØ•Áä∂ÊÄÅ
		if (options && options.status) {
			this.statusFilter = options.status
		}
		this.initPage()
	},
	
	computed: {
	// ÂΩìÂâçÁä∂ÊÄÅÊ†áÁ≠æ
		statusLabel() {
		const found = this.statusList.find(item => item.value === this.statusFilter)
			return found ? found.label : 'ÂÖ®ÈÉ®'
		},
	// ËÆ°ÁÆóÊÄªËçØÊùêÁßçÁ±ªÊï∞
	totalDrugs() {
		const drugSet = new Set()
		this.recordList.forEach(record => {
			if (record.items && Array.isArray(record.items)) {
				record.items.forEach(item => {
					drugSet.add(item.drugName + item.specification)
				})
			}
		})
		return drugSet.size
		},
		dateFilterText() {
			if (!this.startDate && !this.endDate) {
				return 'ÂÖ®ÈÉ®Êó∂Èó¥'
			}
			if (this.selectedQuickFilter === 'today') {
				return '‰ªäÂ§©'
			}
			if (this.selectedQuickFilter === 'week') {
				return 'Êú¨Âë®'
			}
			if (this.selectedQuickFilter === 'month') {
				return 'Êú¨Êúà'
			}
			if (this.startDate && this.endDate) {
				return `${this.startDate} ~ ${this.endDate}`
			}
			if (this.startDate) {
				return `${this.startDate} Ëµ∑`
			}
			if (this.endDate) {
				return `Ëá≥ ${this.endDate}`
			}
			return 'ÂÖ®ÈÉ®Êó∂Èó¥'
		}
	},
	
	onShow() {
		// Âè™Âú®‰ªéÂÖ∂‰ªñÈ°µÈù¢ËøîÂõûÊó∂Âà∑Êñ∞ÔºåÈÅøÂÖçÈáçÂ§çÂä†ËΩΩ
		// Â¶ÇÊûúÂàóË°®‰∏∫Á©∫ÔºåËØ¥ÊòéÂèØËÉΩÊòØÈ¶ñÊ¨°Âä†ËΩΩÔºåÊ≠§Êó∂‰∏çÈúÄË¶ÅÂà∑Êñ∞
		if (this.recordList.length > 0) {
			this.refreshList()
		}
	},
	
	// ‰∏ãÊãâÂà∑Êñ∞
	onPullDownRefresh() {
		this.refreshList()
		setTimeout(() => {
			uni.stopPullDownRefresh()
		}, 1000)
	},
	
	methods: {
	initPage() {
		// Ëé∑ÂèñÂΩìÂâçÁî®Êà∑IDÔºàÂÖºÂÆπ userId Âíå _idÔºâ
		const userInfo = uni.getStorageSync('userInfo')
		this.currentUserId = userInfo?.userId || userInfo?._id || ''
		
		console.log('üìã ÂÖ•Â∫ìÂàóË°®È°µÂàùÂßãÂåñ:', {
			hasUserInfo: !!userInfo,
			currentUserId: this.currentUserId,
			userRole: userInfo?.role
		})
		
		// ËÆæÁΩÆÂΩìÂâçÊó∂Èó¥
		this.updateCurrentTime()
		
		this.selectQuickFilter('month')
		this.loadStats()
	},
		
		updateCurrentTime() {
			const now = new Date()
			const year = now.getFullYear()
			const month = String(now.getMonth() + 1).padStart(2, '0')
			const day = String(now.getDate()).padStart(2, '0')
			this.currentTime = `${year}Âπ¥${month}Êúà${day}Êó•`
		},
		
	async loadRecords() {
		// È¶ñÊ¨°Âä†ËΩΩÊòæÁ§∫ loadingÔºåÂà∑Êñ∞Êó∂‰∏çÊòæÁ§∫
		const isFirstLoad = this.page === 1 && this.recordList.length === 0
		
		try {
			// ‰ΩøÁî® callFunction ÁöÑÁ¨¨‰∏â‰∏™ÂèÇÊï∞ÊéßÂà∂ÊòØÂê¶ÊòæÁ§∫ loading
			const result = await this.$api.callFunction('inRecords', {
				action: 'getList',
				data: {
					status: this.statusFilter,
					page: this.page,
					pageSize: this.pageSize,
					keyword: this.searchKeyword,
					startDate: this.startDate,
					endDate: this.endDate
				}
			}, isFirstLoad)  // Âè™Âú®È¶ñÊ¨°Âä†ËΩΩÊó∂ÊòæÁ§∫ loading
			
			// result Â∑≤ÁªèÊòØÂ§ÑÁêÜÂêéÁöÑÊï∞ÊçÆ‰∫ÜÔºàrequest.js Â∑≤Â§ÑÁêÜÔºâ
			if (result && result.success) {
				let newData = result.data || []
				
				// Â¶ÇÊûúÊúâÊêúÁ¥¢ÂÖ≥ÈîÆËØçÔºåÂú®ÂâçÁ´ØÂÜçËøáÊª§‰∏ÄÊ¨°Ôºà‰ª•Èò≤‰∫ëÂáΩÊï∞‰∏çÊîØÊåÅÔºâ
				if (this.searchKeyword) {
					newData = this.filterByKeyword(newData)
				}
				// ÂÜçÊåâÂΩìÂâçÁä∂ÊÄÅÂÅö‰∏ÄÊ¨°ÂâçÁ´ØËøáÊª§ÔºåÁ°Æ‰øù‚ÄúÂæÖÂ§çÊ†∏‚ÄùÁ≠âÊ†áÁ≠æÂè™ÊòæÁ§∫ÂØπÂ∫îÁä∂ÊÄÅ
				if (this.statusFilter && this.statusFilter !== 'all') {
					newData = newData.filter(item => item.status === this.statusFilter)
				}
				
				this.recordList = this.page === 1 ? newData : [...this.recordList, ...newData]
				this.hasMore = newData.length >= this.pageSize
			} else if (Array.isArray(result)) {
				// ÂÖºÂÆπÁõ¥Êé•ËøîÂõûÊï∞ÁªÑÁöÑÊÉÖÂÜµ
				let newData = result
				if (this.searchKeyword) {
					newData = this.filterByKeyword(newData)
				}
				if (this.statusFilter && this.statusFilter !== 'all') {
					newData = newData.filter(item => item.status === this.statusFilter)
				}
				this.recordList = this.page === 1 ? newData : [...this.recordList, ...newData]
				this.hasMore = newData.length >= this.pageSize
			} else {
				// Â¶ÇÊûúÊ≤°ÊúâÊï∞ÊçÆÔºåËÆæÁΩÆ‰∏∫Á©∫Êï∞ÁªÑ
				this.recordList = this.page === 1 ? [] : this.recordList
				this.hasMore = false
			}
		} catch (err) {
			console.error('Âä†ËΩΩÂ§±Ë¥•:', err)
			// request.js Â∑≤ÁªèÂ§ÑÁêÜ‰∫ÜÈîôËØØÊèêÁ§∫ÔºåËøôÈáåÂè™ËÆ∞ÂΩïÊó•Âøó
			// È¶ñÊ¨°Âä†ËΩΩÂ§±Ë¥•Êó∂ËÆæÁΩÆÁ©∫Êï∞ÊçÆ
			if (isFirstLoad) {
				this.recordList = []
				this.hasMore = false
			}
		}
	},
	
	// ÂâçÁ´ØÂÖ≥ÈîÆËØçËøáÊª§
	filterByKeyword(list) {
		if (!this.searchKeyword) return list
		const keyword = this.searchKeyword.toLowerCase()
		return list.filter(item => {
			// ÊêúÁ¥¢ÂçïÂè∑
			if (item.recordNo && item.recordNo.toLowerCase().includes(keyword)) {
				return true
			}
			// ÊêúÁ¥¢ËçØÊùêÂêçÁß∞
			if (item.items && Array.isArray(item.items)) {
				return item.items.some(drug => 
					drug.drugName && drug.drugName.toLowerCase().includes(keyword)
				)
			}
			return false
		})
	},
	
	async loadCounts() {
		try {
			const result = await this.$api.callFunction('inRecords', {
				action: 'getCounts',
				data: {}
			}, false)  // ‰∏çÊòæÁ§∫ loading
			
			if (result && result.success) {
			// Êõ¥Êñ∞Áä∂ÊÄÅÁªüËÆ°Êï∞ÈáèÔºàÊñ∞ËÆæËÆ°Ôºâ
			this.statusCounts = {
					all: result.all || 0,
					draft: result.draft || 0,
					pending_review: result.pending_review || 0,
					completed: result.completed || 0,
					rejected: result.rejected || 0
				}
			// ‰øùÊåÅÂéüÊúâÁöÑstatusSummaryÂÖºÂÆπÊÄß
			this.statusSummary = this.statusCounts
			} else if (result) {
			this.statusCounts = {
					all: result.all || 0,
					draft: result.draft || 0,
					pending_review: result.pending_review || 0,
					completed: result.completed || 0,
					rejected: result.rejected || 0
				}
			this.statusSummary = this.statusCounts
			}
		} catch (err) {
			console.error('Âä†ËΩΩÊï∞ÈáèÂ§±Ë¥•:', err)
			// request.js Â∑≤Â§ÑÁêÜÈîôËØØÊèêÁ§∫
		}
	},
	
	async loadStats() {
		// ‰∏ç‰ΩøÁî® loadingÔºåÈÅøÂÖçÂΩ±ÂìçÁî®Êà∑‰ΩìÈ™å
		try {
			const result = await this.$api.callFunction('inRecords', {
				action: 'getStats',
				data: {}
			}, false)  // ‰∏çÊòæÁ§∫ loading
			
			if (result && result.success) {
				this.statsData = {
					today: result.today || 0,
					thisWeek: result.thisWeek || 0,
					thisMonth: result.thisMonth || 0,
					pending: result.pending || 0
				}
			} else if (result) {
				// ÂÖºÂÆπÁõ¥Êé•ËøîÂõûÊï∞ÊçÆÁöÑÊÉÖÂÜµ
				this.statsData = {
					today: result.today || 0,
					thisWeek: result.thisWeek || 0,
					thisMonth: result.thisMonth || 0,
					pending: result.pending || 0
				}
			} else {
				// Â¶ÇÊûú getStats Â§±Ë¥•Ôºå‰ΩøÁî® getCounts ‰Ωú‰∏∫Â§áÁî®
				this.fallbackStats()
			}
		} catch (err) {
			console.error('Âä†ËΩΩÁªüËÆ°Â§±Ë¥•:', err)
			// ‰ΩøÁî® getCounts ÁöÑÊï∞ÊçÆ‰Ωú‰∏∫Â§áÁî®
			this.fallbackStats()
		}
	},
	
	async fallbackStats() {
		try {
			const countsResult = await this.$api.callFunction('inRecords', {
				action: 'getCounts',
				data: {}
			}, false)  // ‰∏çÊòæÁ§∫ loading
			
			if (countsResult && countsResult.success) {
				this.statsData.today = countsResult.today || 0
				this.statsData.pending = countsResult.pending_review || 0
				// Êú¨Âë®ÂíåÊú¨Êúà‰ΩøÁî®‰ªäÊó•Êï∞ÊçÆ‰Ωú‰∏∫Âç†‰Ωç
				this.statsData.thisWeek = countsResult.today || 0
				this.statsData.thisMonth = countsResult.completed || 0
			} else if (countsResult) {
				// ÂÖºÂÆπÁõ¥Êé•ËøîÂõûÊï∞ÊçÆÁöÑÊÉÖÂÜµ
				this.statsData.today = countsResult.today || 0
				this.statsData.pending = countsResult.pending_review || 0
				this.statsData.thisWeek = countsResult.today || 0
				this.statsData.thisMonth = countsResult.completed || 0
			}
		} catch (err) {
			console.error('Â§áÁî®ÁªüËÆ°Âä†ËΩΩÂ§±Ë¥•:', err)
			// ‰øùÊåÅÈªòËÆ§ÂÄº 0
		}
	},
		
		resetFilters() {
			this.searchKeyword = ''
			this.startDate = ''
			this.endDate = ''
			this.selectedQuickFilter = 'month'
			this.statusFilter = 'all'
			this.tempStatus = 'all'
			this.page = 1
			this.recordList = []
			this.hasMore = true
			this.selectQuickFilter('month')
			this.generateList()
		},
		
		generateList() {
			this.page = 1
			this.recordList = []
			this.hasMore = true
			this.loadRecords()
			this.loadCounts()
		},
		
	// ÂàáÊç¢Áä∂ÊÄÅÁ≠õÈÄâÔºàÊñ∞ÊñπÊ≥ïÔºâ
	changeStatusFilter(status) {
		this.statusFilter = status
		this.page = 1
		this.recordList = []
		this.hasMore = true
		this.loadRecords()
		
		// ÊåØÂä®ÂèçÈ¶à
		uni.vibrateShort({ type: 'light' })
		},
		
		refreshList() {
			this.page = 1
			this.recordList = []
			this.hasMore = true
			this.updateCurrentTime()
			// Âà∑Êñ∞Êó∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆ
			this.loadRecords()
			this.loadCounts()
			this.loadStats()
		},
		
		// ÈùôÈªòÂà∑Êñ∞Ôºà‰∏çÊòæÁ§∫ loadingÔºâ
		silentRefresh() {
			this.loadRecords()
			this.loadCounts()
			this.loadStats()
		},
		
		loadMore() {
			if (!this.hasMore) return
			this.page++
			this.loadRecords()
		},
		
		getStatusText(status) {
			const statusMap = {
				draft: 'ËçâÁ®ø',
				pending_review: 'ÂæÖÂ§çÊ†∏',
				completed: 'Â∑≤ÂÆåÊàê',
				rejected: 'Â∑≤È©≥Âõû'
			}
			return statusMap[status] || status
		},
		
	canReview(item) {
		// ÂæÖÂ§çÊ†∏ÁöÑÂçïÊçÆÔºå‰∏î‰∏çÊòØËá™Â∑±ÂàõÂª∫ÁöÑÔºå‰∏îÂΩìÂâçÁî®Êà∑ÊúâÂ§çÊ†∏ÊùÉÈôê
		console.log('üîç Â§çÊ†∏ÊùÉÈôêÊ£ÄÊü•:', {
			itemStatus: item.status,
			operatorId: item.operatorId,
			currentUserId: this.currentUserId,
			isSameUser: item.operatorId === this.currentUserId
		})
		
		if (item.status !== 'pending_review' || item.operatorId === this.currentUserId) {
			console.log('‚ùå ‰∏çËÉΩÂ§çÊ†∏: Áä∂ÊÄÅ‰∏çÂØπÊàñÊòØËá™Â∑±ÂàõÂª∫ÁöÑ')
			return false
		}
		
		// Ê£ÄÊü•Áî®Êà∑ËßíËâ≤ÊòØÂê¶ÊúâÂ§çÊ†∏ÊùÉÈôêÔºàÁÆ°ÁêÜÂëòÊàñÈ°πÁõÆÁªèÁêÜÔºâ
		const userInfo = uni.getStorageSync('userInfo')
		const userRole = userInfo?.role || ''
		const hasPermission = userRole === 'admin' || userRole === 'project_manager'
		
		console.log('‚úÖ Â§çÊ†∏ÊùÉÈôêÁªìÊûú:', {
			userRole,
			hasPermission
		})
		
		return hasPermission
	},
		
		goDetail(id, item) {
			// Â¶ÇÊûúÊòØÂæÖÂ§çÊ†∏Áä∂ÊÄÅ‰∏îÂèØ‰ª•Â§çÊ†∏ÔºåË∑≥ËΩ¨Âà∞Â§çÊ†∏È°µÈù¢
			if (item && item.status === 'pending_review' && this.canReview(item)) {
				this.goReview(id)
			} else {
