	// ========== ÊêúÁ¥¢Áõ∏ÂÖ≥ ==========
	onKeywordUpdate(val) {
		this.searchKeyword = val
		if (this.searchTimer) clearTimeout(this.searchTimer)
		this.searchTimer = setTimeout(() => this.generateList(), 400)
	},
	onStartDateUpdate(val) {
		this.startDate = val
	},
	onEndDateUpdate(val) {
		this.endDate = val
	},
	onDateRangeChange({ start, end }) {
		this.startDate = start || ''
		this.endDate = end || ''
		this.generateList()
	},

	selectQuickFilter(value) {
		this.selectedQuickFilter = value
		const today = new Date()
		
		switch(value) {
			case 'all':
				this.startDate = ''
				this.endDate = ''
				break
			case 'today':
				this.startDate = this.formatDate(today)
				this.endDate = this.formatDate(today)
				break
			case 'week':
				const weekStart = new Date(today)
				weekStart.setDate(today.getDate() - today.getDay())
				this.startDate = this.formatDate(weekStart)
				this.endDate = this.formatDate(today)
				break
			case 'month':
				const monthStart = new Date(today.getFullYear(), today.getMonth(), 1)
				this.startDate = this.formatDate(monthStart)
				this.endDate = this.formatDate(today)
				break
			case 'custom':
				// Ëá™ÂÆö‰πâÁî±Êó•ÊúüÈÄâÊã©Âô®ÂõûË∞ÉÊéßÂà∂
				break
		}
		if (value !== 'custom') {
			this.generateList()
		}
	},
	
	formatDate(date) {
		const year = date.getFullYear()
		const month = String(date.getMonth() + 1).padStart(2, '0')
		const day = String(date.getDate()).padStart(2, '0')
		return `${year}-${month}-${day}`
	},
	
